.modal-substrate {
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #0000008f;
  z-index: 888;
  width: 100vw;
  height: 100vh; }
.hide {
  display: none; }
.show {
  display: block; }
.modal {
  position: absolute;
  border: 1px solid $grey;
  border-radius: 10px;
  background-color: $white;
  text-align: center;
  width: 45%;
  padding: 2%;
  bottom: 10%;
  margin: 15% auto;
  z-index: 999;
  & a.btn {
    cursor: pointer;
    display: inline-block;
    color: $white;
    padding: 1% 2%; } }
